* {
    margin: 0;
    padding: 0;
    font-family: "Poppins", sans-serif;
    transition: all 0.3s ease-in-out;
    outline: none;
    scroll-behavior: smooth;
}

html {
    font-size: 10px;
}

li {
    list-style: none;
    text-decoration: none;
}

.btn {
    outline: none;
    border: none;
    cursor: pointer;
    background: none;

    display: grid;
    place-items: center;
}

input {
    caret-color: #3abe2f;
    outline: none;
}

a {
    cursor: pointer !important;
    text-decoration: none;
}

.d-none {
    display: none;
}